<template>
  <div id="bannerCard" :style="bannerCardCss" @click="clickHandle">
    <van-image fit="scale-down" :src="banner_src" />
    <span>
      <p @click="authorPage">图来自 风生白衣</p>
    </span>
  </div>
</template>

<script>
let clickTimer
export default {
  name: 'bannerCard',
  data () {
    return {
      banner_src: null,
      bannerCardCss: null
    }
  },
  mounted () {
    this.getBanner(new Date().getMonth() + 1)
    this.bannerCardAnimation()
  },
  methods: {
    bannerCardAnimation () {
      setTimeout(() => {
        this.bannerCardCss = 'transform: scale(1);opacity: 1;'
      }, 0)
    },
    clickHandle () {
      clearTimeout(clickTimer)
      this.bannerCardCss = 'transform: scale(.9);opacity: .5;'
      clickTimer = setTimeout(() => {
        this.bannerCardCss = 'transform: scale(1);opacity: 1;'
      }, 100)
    },
    getBanner (Month) {
      const spring = 'http://150.158.149.195/images/banner/spring.jpg'
      const summer = 'http://150.158.149.195/images/banner/summer.jpg'
      const autumn = 'http://150.158.149.195/images/banner/autumn.jpg'
      const winter = 'http://150.158.149.195/images/banner/winter.jpg'
      const arr = [winter, winter, spring, spring, spring, summer, summer, summer, autumn, autumn, autumn, winter]
      this.banner_src = arr[Month]
    },
    authorPage () {
      window.open('https://www.zcool.com.cn/work/ZNDQ5MTcxMDg=.html', '_blank')
    }
  }
}
</script>

